import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
const Royalty = resolve => require(['@/pages/royalty'], resolve)

// subPoyalPage
const MainRule = resolve => require(['@/pages/subRoyalPages/MainRule'], resolve)
const OperateRes = resolve => require(['@/pages/subRoyalPages/operateRes'], resolve)
const ParamConfig = resolve => require(['@/pages/subRoyalPages/paramConfig'], resolve)
const RuleConfig = resolve => require(['@/pages/subRoyalPages/ruleConfig'], resolve)

// thirdRouter
const tagRuleConfig = resolve => require(['@/pages/subRoyalPages/ruleConfigPages/tag'], resolve)
const orderRuleConfig = resolve => require(['@/pages/subRoyalPages/ruleConfigPages/order'], resolve)
const memberRuleConfig = resolve => require(['@/pages/subRoyalPages/ruleConfigPages/member'], resolve)
// ————————————————————————————————————————————————————————————————————————————————————————————————————————
const ruleParamConfig = resolve => require(['@/pages/subRoyalPages/paramConfigPages/rule'], resolve)
const rewardParamConfig = resolve => require(['@/pages/subRoyalPages/paramConfigPages/reward'], resolve)

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Royalty',
      component: Royalty,
      redirect: '/mainRule',
      children: [
        {
          path: '/mainRule',
          name: 'MainRule',
          component: MainRule
        }, {
          path: '/operateRes',
          name: 'OperateRes',
          component: OperateRes
        }, {
          path: '/paramConfig',
          name: 'ParamConfig',
          component: ParamConfig,
          redirect:'/paramConfig/rule',
          children:[
            {
              path: 'rule/',
              name: 'ruleParamConfig',
              component: ruleParamConfig
            },{
              path: 'reward/',
              name: 'rewardParamConfig',
              component: rewardParamConfig
            }
          ]
        }, {
          path: '/ruleConfig',
          name: 'RuleConfig',
          component: RuleConfig,
          redirect:'/ruleConfig/tag',
          children: [
            {
              path: 'tag/',
              name: 'tagRuleConfig',
              component: tagRuleConfig
            }, {
              path: 'order/',
              name: 'orderRuleConfig',
              component: orderRuleConfig
            }, {
              path: 'member/',
              name: 'memberRuleConfig',
              component: memberRuleConfig
            }
          ]
        }

      ]
    }
  ]
})



